/* Global */
* {
  margin: 0;
  padding: 0;
  font-family: "Inter", sans-serif;
}

body {
  background-color: #e0e0e0;
  color: #fff;
}

.container {
  width: 80%;
  margin: auto;
}

/* Business Card */
.business-card {
  background-color: #1a1b21;
  border-radius: 10px;
  width: 400px;
  margin: 2rem auto;
}

#profile-picture {
  object-fit: contain;
  border-radius: inherit;
  width: 100%;
}

#info {
  margin: 1rem auto;
  text-align: center;
}

#info #name {
  font-weight: 600;
}

#info #designation {
  font-size: 0.8rem;
  font-weight: 400;
  color: rgb(255, 208, 0);
}

#info #business-contacts {
  margin: 1rem auto;
  display: flex;
  justify-content: center;
  gap: 1rem;
}

#info #business-contacts a {
  font-size: 1rem;
  width: 125px;
  padding: 0.5rem 0;
  border-radius: 8px;
  text-decoration: none;
  background-color: #fff;
  color: #000;
}
#info #business-contacts #linkedin-link {
  background-color: #5093e2;
  color: #fff;
}

#about {
  margin-top: 4rem;
}

#about h3 {
  margin: 0.5rem 0;
}

#interests {
  margin-top: 1rem;
}

#interests h3 {
  margin: 0.5rem 0;
}

#socials {
  margin-top: 2rem;
}

#socials #social-links {
  list-style-type: none;
  display: flex;
  justify-content: center;
  gap: 2rem;
  font-size: 2rem;
  padding-bottom: 1rem;
}

#socials #social-links a {
  color: #fff;
}
